import React, {useContext, useState} from "react";
import "./StartupProjects.scss";
import {bigProjects} from "../../portfolio";
import {Fade} from "react-reveal";
import StyleContext from "../../contexts/StyleContext";

export default function StartupProject() {
  function openUrlInNewTab(url) {
    if (!url) {
      return;
    }
    var win = window.open(url, "_blank");
    win.focus();
  }

  const {isDark} = useContext(StyleContext);

  // Collect unique platforms/categories for chips and their counts
  const platformCounts = {};
  bigProjects.projects.forEach((p) => {
    if (Array.isArray(p.platforms)) {
      p.platforms.forEach((platform) => {
        platformCounts[platform] = (platformCounts[platform] || 0) + 1;
      });
    } else if (p.platforms) {
      platformCounts[p.platforms] = (platformCounts[p.platforms] || 0) + 1;
    }
  });

  // Sort platforms by count descending
  const sortedPlatforms = Object.entries(platformCounts)
    .sort((a, b) => b[1] - a[1])
    .map(([platform]) => platform);

  // State for selected filter
  const [selectedPlatform, setSelectedPlatform] = useState("All");

  // Filter projects based on selected chip
  const filteredProjects =
    selectedPlatform === "All"
      ? bigProjects.projects
      : bigProjects.projects.filter((p) =>
          Array.isArray(p.platforms)
            ? p.platforms.includes(selectedPlatform)
            : p.platforms === selectedPlatform
        );

  if (!bigProjects.display) {
    return null;
  }
  return (
    <div className="main" id="projects">
      <h1 className="skills-heading">{bigProjects.title}</h1>
      <p
        className={
          isDark
            ? "dark-mode project-subtitle"
            : "subTitle project-subtitle"
        }
      >
        {bigProjects.subtitle}
      </p>
      {/* Filter Chips */}
      <div className="project-filter-chips" style={{marginBottom: "1rem"}}>
        <span
          className={`project-chip${selectedPlatform === "All" ? " active" : ""}`}
          onClick={() => setSelectedPlatform("All")}
        >
          All&nbsp;
          <span style={{fontWeight: "normal"}}>
            ({bigProjects.projects.length})
          </span>
        </span>
        {sortedPlatforms.map((platform) => (
          <span
            key={platform}
            className={`project-chip${selectedPlatform === platform ? " active" : ""}`}
            onClick={() => setSelectedPlatform(platform)}
          >
            {platform}&nbsp;
            <span style={{fontWeight: "normal"}}>
              ({platformCounts[platform]})
            </span>
          </span>
        ))}
      </div>
      <div className="projects-container">
        {filteredProjects.map((project, i) => {
          return ( <Fade bottom duration={1000} distance="20px">
             <div
                  key={i}
                  className={
                    isDark
                      ? "dark-mode project-card project-card-dark"
                      : "project-card project-card-light"
                  }
                >
                   
                  <div className="project-content"> 
                    <div className="project-header">
                    {project.image ? (
                      <div className="project-image-container">
                        <img
                          src={project.image}
                          alt={project.projectName}
                          className="project-image"
                        ></img>
                      </div>
                    ) : null} 
                    <div className="card-title-container">
                    <h5
                        className={isDark ? "dark-mode card-title" : "card-title"}
                      >
                        {project.projectName}
                    </h5>
                    <p className="project-platforms"> {project.platforms.join(' ')}</p></div>
                    </div>
                    <div className="project-detail"> 
                    <p
                      className={
                        isDark ? "dark-mode card-subtitle" : "card-subtitle"
                      }
                    >
                      {project.projectDesc}
                      {project.descBullets ? <ul>{project.descBullets.map((item, i) => (
          <li
            key={i}
            className={isDark ? "subTitle dark-mode-text" : "subTitle"}
          >
            {item}
          </li>
        ))}</ul>
      : null }
                    </p>
                    {project.footerLink ? (
                      <div className="project-card-footer">
                        {project.footerLink.map((link, i) => {
                          return (
                            <span
                              key={i}
                              className={
                                isDark ? "dark-mode project-tag" : "project-tag"
                              }
                              onClick={() => openUrlInNewTab(link.url)}
                            >
                              {link.name}
                            </span>
                          );
                        })}
                      </div>
                    ) : null}
                    </div>
                  </div>
                  <div className="project-gallery">
                    <figure className="project-gallery-figure">
                      <img className="project-gallery-image" src={project.image}></img>
                    </figure>
                     <figure className="project-gallery-figure">
                      <img className="project-gallery-image" src={project.image}></img>
                    </figure>
                     <figure className="project-gallery-figure">
                      <img className="project-gallery-image" src={project.image}></img>
                    </figure>
                    
                     
                  </div>
                
                </div> </Fade>
          );
        })}
      </div>
    </div>
  );
}