import React, { useState, useRef, useEffect } from "react";
import "./Button.scss";

export default function Button({ text, className, href, newTab, options = [] }) {
  const [showOptions, setShowOptions] = useState(false);
  const dropdownRef = useRef(null);

  useEffect(() => {
    function handleClickOutside(event) {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(event.target)
      ) {
        setShowOptions(false);
      }
    }
    if (showOptions) {
      document.addEventListener("mousedown", handleClickOutside);
    } else {
      document.removeEventListener("mousedown", handleClickOutside);
    }
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [showOptions]);

  const handleButtonClick = (e) => {
    if (options.length > 0) {
      e.preventDefault();
    }
    setShowOptions((prev) => !prev); 
    if (window.gtag) {
      window.gtag('event', text, {
        event_category: 'interaction',
        event_label: text, 
        value: 1
      });
    }
  };

  const handleOptionClick = (e, option) => {
    setShowOptions(false);
    if (window.gtag) {
      window.gtag('event', option.name, {
        event_category: 'interaction',
        event_label: option.name,
        value: 1
      });
    }
  };

  return (
    <div className={className} style={{ position: "relative" }} ref={dropdownRef}>
      <a
        className="main-button"
        href={href}
        target={newTab ? "_blank" : undefined}
        onClick={handleButtonClick}
      >
        {text}
        {options.length > 0 && <i className="fas fa-chevron-down"></i>}
      </a>
      {showOptions && options.length > 0 && (
        <div className="button-dropdown">
          {options.map((option, idx) => (
            <a
              key={idx}
              href={option.href}
              className="dropdown-option"
              target={newTab ? "_blank" : undefined}
              onClick={(e) => handleOptionClick(e, option)}
            >
              {option.name}
            </a>
          ))}
        </div>
      )}
    </div>
  );
}
